import React, { Component } from 'react';
import firebase, {database} from 'firebase'

import Fragmento from './componentes/Fragmento'
import LogoIEM from './componentes/LogoIEM'
import EntradaActiva from './componentes/EntradaActiva'

import logoFuba from './Media/logo_fuba.png'
import logoSemillero from './Media/logo_semillero.png'

import 'dragscroll'

const config = {
  apiKey: "AIzaSyD7DrODZgtQLb1o0PuBDrG1HG0StH-d0eA",
  authDomain: "iem-interactivo.firebaseapp.com",
  databaseURL: "https://iem-interactivo.firebaseio.com",
  projectId: "iem-interactivo",
  storageBucket: "iem-interactivo.appspot.com",
  messagingSenderId: "118016638881"
};

firebase.initializeApp(config);

class App extends Component {

  state = {
      entradas: [],
      cargandoPagina: true,
      entradaActiva: null
    }
  

  componentWillMount() {
    database().ref('entradas')
    .once('value')
    .then( snap => {
      let entradas = []
      let entradaSnap = snap.val()
      for (const key in entradaSnap) {
        if (entradaSnap.hasOwnProperty(key)) {
          entradas.push(entradaSnap[key])
        }
      }
      this.setState({entradas, cargandoPagina: false})
    })
  }

  mostrarEntrada = (i) => {
    if (this.state.entradaActiva !== null) {
      setTimeout( () => {this.setState( {entradaActiva: i} )}, 1000); 
    } else {
      this.setState( prevState => ({entradaActiva: i}) )
    }
      
  }

  render() {
    const cssCarga = this.state.cargandoPagina ? "cargando" : "cargado",
          fragmentos = this.state.entradas

    return (
      <div className={ `App dragscroll ${cssCarga}` }>
        <div className="fondoApp" ></div>
        <div id="logos">
          <a href="http://bellasartesmed.edu.co/" target="_Blank" ><img src={logoFuba} alt="" /></a>
          <a href="http://fubatransmedia.online/" target="_Blank" ><img src={logoSemillero} alt="" /></a>
        </div>

        <div id="contenedor" >
          <LogoIEM pos={this.state.pos} />

          { /* __RENDER DE TODA LA LISTA DE ENTRADAS__ */
            fragmentos.map( (entrada , i, arr) => {
              const length = arr.length 
              let ang = ( (Math.PI * 2) / length ) * i
              let r = 500
              let x = r * Math.cos(ang)
              let y = r * Math.sin(ang)

              return <Fragmento titulo={entrada.titulo}
                      imagen={entrada.portadaURL}
                      onClick = { (e) => {this.mostrarEntrada(i)} }
                      x={x} y={y} key={i} index={i} />
            }
            )
          }
        </div>
        
        <EntradaActiva  entrada={fragmentos[this.state.entradaActiva]}
                        cerrar={ () => {this.mostrarEntrada(null)} } />

      </div>
    );
  }
}

export default App;
