import React, {Component} from 'react';

const generarPuntos = (r) => {
  let dosPI = 2 * Math.PI, vectores = 5,
      puntos = []

  for (let ang = 0; ang < dosPI; ang += dosPI / vectores ) {
    let vAng = Math.random()
    let x = r * Math.cos(vAng - ang)
    let y = r * Math.sin(vAng - ang)
    puntos.push({x, y})
  }
  return puntos
}

class Fragmento extends Component {
  constructor(props) {
    super(props);
    this.puntos = []
  }

  componentWillMount() {
    this.puntos = generarPuntos(50)
  }
  
  render() {
    const {titulo, imagen, x, y, onClick} = this.props
    let clipPolygon = [],
        linePolygon = ""

    this.puntos.forEach( (cord, i) => {
      clipPolygon[i] = `${cord.x + 50}% ${cord.y + 50}%`
      linePolygon = linePolygon.concat(`${cord.x},${cord.y} `)
    });

    return (
      <div className="fragmento" style={{left: `calc( 50% + ${x}px )`, top: `calc( 50% + ${y}px )`}}>
        <div className="imagen" style={{clipPath: `polygon( ${clipPolygon}` }} onClick={onClick} >
          <div style={{ backgroundImage: ` url( ${imagen} ) ` }} />
        </div>
        <svg  viewBox="0 0 100 100"
              xmlns="http://www.w3.org/2000/svg">
          <polygon transform="translate(50 50)" points={linePolygon}/>
        </svg>
        <h1>{titulo}</h1>
      </div>
    );
  }

}

export default Fragmento;


