import React, { Component } from 'react';
import Lightbox from 'react-images'

class Galeria extends Component {
  constructor(props) {
    super(props)
    this.state = {
      IsOpen: false,
      currentImage: 0
    }
    this.closeLightbox = this.closeLightbox.bind(this);
    this.openLightbox = this.openLightbox.bind(this);
    this.gotoNext = this.gotoNext.bind(this);
    this.gotoPrevious = this.gotoPrevious.bind(this);
  }

  openLightbox(event) {
    let index = Number(event.target.getAttribute("dataindex"))
    this.setState({
      currentImage: index,
      IsOpen: true,
    });
  }
  closeLightbox() {
    this.setState({
      currentImage: 0,
      IsOpen: false,
    });
  }
  gotoPrevious() {
    this.setState({
      currentImage: this.state.currentImage - 1,
    });
  }
  gotoNext() {
    this.setState({
      currentImage: this.state.currentImage + 1,
    });
  }

  render() {
    let {galeria, titulo} = this.props
    let fotos = []
    return (
      <div className="galeria">
        <div className="fotos">
          { galeria.map( (foto, i) => {
            fotos.push({src: foto.fotoURL })
            return <img key={i} dataindex={i} src={foto.fotoURL} alt={`${titulo}_foto${i}`} height="200" onClick={this.openLightbox} />
          }
          )
          }
        </div>
        <Lightbox images={fotos}
            isOpen={this.state.IsOpen}
            currentImage={this.state.currentImage}
            onClickPrev={this.gotoPrevious}
            onClickNext={this.gotoNext}
            onClose={this.closeLightbox}
            imageCountSeparator = " / "
        />
      </div>
    );
  }
}

export default Galeria;


