import React from 'react';

const c = {
  oscuro: "#111111",
  grisOscuro: "#212121",
  grisClaro: "#666666",
  claro: "#999999"
}

const puntos = [
  "4.5,99.7 4.5,366.1 62.8,198.5 ",
  "154.2,169.2 83.5,209.4 12,404 383.8,404 387.7,402.7 ",
  "138.9,150.6 4.5,13.3 4.5,61.1 79.4,184.5 ",
  "404,13 404,4.6 19.2,4.6 158.1,143.4 ",
  "402.6,387.9 404,383.1 404,34.4 173.7,159.1 "
]

const LogoMenu = ({pos}) => {

  return (
    <div className="logo-contenedor" >
      <div className="fondo" style={{ background:c.grisClaro }} ></div>
      <div className="atras">
        <svg xmlns="http://www.w3.org/2000/svg" viewBox="0 0 408.5 408.5" >
          { puntos.map( (poligono , i) => (
            <polygon fill={c.claro} points={ poligono } key={i} />
          ) )
          }
        </svg>
      </div>
      <div className="frente" style={{ background: c.grisOscuro }} >
        <svg xmlns="http://www.w3.org/2000/svg" viewBox="0 0 408.5 408.5" >
          { puntos.map( (poligono, i) => (
            <polygon fill={c.oscuro} points={ poligono } key={i} />
          ) )
          }
        </svg>
      </div>
      <div className="menu">
        <h1>IEM</h1>
        <h2>Imágenes, Encuentro y Memoria</h2>
      </div>
    </div>
  );
}

export default LogoMenu;


